import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { getCollectionList } from '../../../../../Services/Actions/homeActions';
class AssetsCollections extends Component {
    state = {
        addCollectionShowModal: false, collectionList: { assets: 0, collection: [], collectionCount: 0 }
        , myAssetsData: [],
        collectionListRequest: true,
        assetsRequest: true,
        perPage: 10, currentPage: 1,
        pageLimitOption: { value: '10', label: '10' }
    };
    
    componentDidMount() {
        this.onLoadCollectionList()
    }


    addCollectionModal = event => this.setState({
        addCollectionShowModal: true
    })

    onLoadCollectionList = () => {
        var self = this;
        this.setState({ collectionListRequest: true })
        getCollectionList().then((response) => {
            const { responseData } = response.data;
            if (responseData && Object.keys(responseData).length > 0) {
                self.setState({ collectionListRequest: false, collectionList: { assets: responseData.assets, collection: responseData.collection, collectionCount: responseData.collection && responseData.collection.length > 0 ? responseData.collection.length : 0 } })
            }
            else {
                self.setState({ collectionListRequest: false })
            }
        }).catch((ex) => {
            self.setState({ collectionListRequest: false })
        })

    }
    addCollectionCallback = (addCollectionEvent) => {
        this.setState({
            addCollectionShowModal: addCollectionEvent
        })
    }
    addCollectionCallbackWithAPI = (addCollectionEvent) => {
        this.onLoadCollectionList()
        this.setState({
            addCollectionShowModal: addCollectionEvent
        })
    }
    render() {
        const { collectionList, addCollectionShowModal, myAssetsData, collectionListRequest, assetsRequest, perPage, currentPage, pageLimitOption } = this.state;

        console.log('collectionList', collectionList);

        return <div className="row mb-xl-3 mb-2">
            {
                collectionList.collection.map((item, cIndex) => {
                    return <div className='col-20' key={cIndex}>
                        <Link to={item.url} className="card card-custom gutter-b text-decoration-none">
                            <div className="card-body d-flex flex-column p-2">
                                <div className="d-flex align-items-center mr-2 w-100">
                                    <div className="symbol symbol-54 flex-shrink-0 me-2 bg-light">
                                        {item.thumbnails ? <img className="img-fluid object-center-cover" src={item.thumbnails} height={66} width={66} alt="" /> : null}
                                    </div>
                                    <div className="overflow-hidden">
                                        <p className="fs-7 fw-semibold mb-1 text-gray-800 text-ellipsis">{item.name}</p>
                                        <p className="mb-0 text-gray-600 text-ellipsis">{item.assets} Assets</p>
                                    </div>
                                </div>
                            </div>
                        </Link>
                    </div>

                })
            }
            <div className='col-20'>
                <button type="button" className="card rounded-1 card-custom mb-sm-0 text-decoration-none w-100 p-0" onClick={this.props.collectionEvent}>
                    <div className="card-body d-flex flex-column p-2">
                        <div className="d-flex align-items-center">
                            <div className="btn bg-light btn-icon flex-shrink-0 h-54px w-54px me-3 block-center">
                                <span className="svg-icon svg-icon-3 me-0">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="54" height="54" viewBox="0 0 59 59">
                                        <g data-name="plus icon" transform="translate(-678 -295.5)">
                                            <path data-name="Path 1" d="M28.5,58a1,1,0,0,1-1-1V29.5H0a1,1,0,0,1,0-2H27.5V0a1,1,0,0,1,2,0V27.5H57a1,1,0,0,1,0,2H29.5V57A1,1,0,0,1,28.5,58Z" transform="translate(679 296.5)" fill="#707070" />
                                        </g>
                                    </svg>
                                </span>
                            </div>
                            <div className="overflow-hidden text-start">
                                <p className="fs-7 fw-semibold mb-1 text-gray-800 text-ellipsis">Create Collections</p>
                                <p className="mb-0 text-gray-600 text-ellipsis">0 Assets</p>
                            </div>
                        </div>
                    </div>
                </button>
            </div>
        </div>
    }
}

export default AssetsCollections;